* {
  box-sizing: border-box;
}

:root {
  --color-primary: #08a6e4;
  --color-primar-shadow: rgba(7, 172, 230, .16);
  --color-redish: #FB3B64;
  --color-green: #55DFB4;
  --color-black: #000000;
  --color-grey: #A5A5A5;
  --color-light-grey: #F7F7F7;
  --color-white: #FFFFFF;
  --font-raleway: "Raleway", sans-serif;
  --font-open-sans: "Open Sans", sans-serif;
}

html {
  font-family: var(--font-raleway);
}

.header {
  height: 70px;
  box-shadow: 0px 3px 6px #00000029;
  opacity: 1;
  margin: 0 auto;
  padding: 0 136px;
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.logo {
  text-decoration: none;
  font: 37px Raleway;
  font-weight: 300;
  color: #08A6E4;
}

.header-menu {
  display: flex;
}

.menu__element {
  padding: 0 26px;
}

.menu__element:last-child {
  padding-right: 0px;
}

.menu__link {
  color: #A5A5A5;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 15px;
  font-weight: bold;
  padding: 28px 18px 22px;
  border-bottom: 4px solid transparent;
}

.menu__link:hover {
  color: var(--color-black);
  border-color: var(--color-primary);
}

.container {
  margin-inline: auto;
  width: min(90%, 70.5rem);
}

/* Page 1 */
.welcome {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.welcome__content {
  margin-top: 130px;
  text-align: center;
}

.welcome__header {
  font-style: var(--font-raleway);
  color: var(--color-black);
  font-size: 49px;
}

.welcome__text {
  font-style: var(--font-open-sans);
  color: var(--color-black);
  margin-top: 20px;
  margin-bottom: 38px;
}

.btn {
  border-radius: 40px;
  text-decoration: none;
  font-weight: bold;
  display: inline-block;
}

.btn--large {
  background-color: var(--color-primary);
  color: var(--color-white);
  font-style: var(--font-open-sans);
  height: 67px;
  width: 440px;
  padding: 22px 150px;
  box-shadow: 5px 10px 14px var(--color-primar-shadow);
}

.welcome__img {
  width: 100%;
  height: auto;
}

/* Page 2 */
.stats {
  margin-top: 100px;
}

.container {
  display: flex;
  flex-direction: column;
}

.stats__container {
  max-width: 800px;
  margin: 100px auto 120px;
  display: flex;
  justify-content: space-between;
}

.stats__title {
  font-style: var(--font-raleway);
  color: var(--color-black);
  font-size: 36px;
  text-align: center;
}

.stats__single {
  max-width: 160px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  text-align: center;
  font-family: var(--font-raleway);
}

.stats__circle {
  height: 63px;
  width: 63px;
  border-radius: 50%;
  position: relative;
}

.stats__number {
  color: var(--color-black);
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  margin-top: 13px;
}

.stats__img {
  position: absolute;
}

.stats__description {
  color: var(--color-black);
  font-size: 17px;
  margin-top: 19px;
}

.stats__single:nth-child(1) .stats__circle {
  background-color: var(--color-redish);
}

.stats__single:nth-child(1) .stats__img {
  top: 0;
  left: 45%;
}

.stats__single:nth-child(2) .stats__circle {
  background-color: var(--color-primary);
}

.stats__single:nth-child(2) .stats__img {
  top: 14px;
  left: 47%;
}

.stats__single:nth-child(3) .stats__circle {
  background-color: var(--color-green);
}

.stats__single__single:nth-child(3) .stats__img {
  top: 6px;
  left: 50%;
}

.benefits__container {
  height: 1395px;
  width: 100vw;
  background-color: var(--color-light-grey);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 105px;
}

.row1 {
  height: 370px;
  width: 1124px;
  background-color: var(--color-white);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 50px;
}

.row1a {
  height: 188px;
  width: 416px;
}

.row1a__text {
  border-bottom: 4px solid var(--color-redish);
  padding-bottom: 40px;
}

.row2b__text {
  border-bottom: 4px solid var(--color-primary);
  padding-bottom: 40px;
}

.row3a__text {
  border-bottom: 4px solid var(--color-green);
  padding-bottom: 40px;
}

.row1a__title,
.row2b__title,
.row3a__title {
  font-size: 36px;
  margin-bottom: 30px;
}

.row1b {
  height: 294px;
  width: 433px;
  position: relative;
}

.row1b-background {
  height: 205px;
  width: 205px;
  background-color: var(--color-redish);
  border-radius: 41px;
  position: absolute;
  right: 0;
  top: 10px;
  transform: rotate(50deg);
}

.row1b-img {
  height: 219px;
  width: 386px;
  position: absolute;
  bottom: 0;
  left: 10px;
}

.row2 {
  height: 370px;
  width: 1124px;
  background-color: var(--color-white);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 50px;
}

.row2b {
  height: 188px;
  width: 416px;
}

.row2a {
  height: 320px;
  width: 320px;
  background-color: var(--color-primary);
  border-radius: 50%;
}

.row2a-img {
  height: 328px;
  width: 426px;
}

.row3 {
  height: 370px;
  width: 1124px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.row3a {
  height: 370px;
  width: 623px;
  background-color: var(--color-white);
  padding: 70px 35px;
}

.row3b {
  height: 370px;
  width: 440px;
  background-color: var(--color-green);
  display: flex;
  justify-content: center;
  align-items: center;
}

.row3b-trumpet {
  padding-top: 60px;
  height: 295px;
  width: 298px;
}

/* Pricing */
.pricing__title {
  padding: 100px 0;
  font-size: 36px;
  font-style: var(--font-raleway);
}

.pricing__container {
  width: 1266px;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: relative;
  margin-bottom: 150px;
}

.pricing__container-element {
  height: 461px;
  width: 372px;
  border: 1px solid var(--color-redish);
  border-radius: 18px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
}

.professional {
  height: 500px;
  width: 405px;
  border-color: var(--color-primary);
  box-shadow: 5px 10px 27px #07ACE629;
}

.professional .pricing__container-element-price {
  color: var(--color-primary);
}

.premium {
  border-color: var(--color-green);
  box-shadow: 5px 10px 27px #07ACE629;
}

.premium .pricing__container-element-price {
  color: var(--color-green);
}

.pricing__container-element-title {
  margin-top: 34px;
  margin-bottom: 14px;
  font-weight: bold;
}

.pricing__container-element-price {
  color: var(--color-redish);
  font-size: 46px;
}

.pricing__container-element-subline {
  margin-top: 5px;
}

.pricing__container-element-list {
  font-style: var(--font-open-sans);
  padding-top: 64px;
}

.pricing__container-element-list-item {
  padding: 0;
  margin: 20px;
}

.pricing__container-element-list-item:nth-child(1),
.pricing__container-element-list-item:nth-child(2),
.pricing__container-element-list-item:nth-child(3),
.pricing__container-element-list-item:nth-child(4),
.pricing__container-element-list-item:nth-child(5),
.premium .pricing__container-element-list .pricing__container-element-list-item:nth-child(5) {
  background: url(../assets/checkmark-pricing-green.svg) no-repeat;
  padding-left: 20px;
}

.basic .pricing__container-element-list .pricing__container-element-list-item:nth-child(4),
.pricing__container-element-list-item:nth-child(5) {
  background: url(../assets/checkmark-pricing-red.svg) no-repeat;
}

.button-pricing {
  width: 150px;
  height: 50px;
  background-color: var(--color-redish);
  border: none;
  border-radius: 23px;
  font-size: 13px;
  color: var(--color-white);
  position: absolute;
  bottom: 0;
  transform: translate(0, 50%);
}

.basic .pricing__container-element-price {
  margin-bottom: 20px;
}

.basic .button-pricing {
  transform: translate(0, 5%);
}

.premium .button-pricing {
  transform: translate(0, 5%);
  background-color: var(--color-green);
}

.professional .button-pricing {
  background-color: var(--color-primary);
}

.contact__container {
  width: 1124px;
  height: 376px;
}

.contact__container__box-left__title {
  font-size: 49px;
  font-style: var(--font-raleway);
  padding-bottom: 22px;
}

.contact__container__box-left__text {
  font-size: 16px;
  font-style: var(--font-open-sans);
  padding-bottom: 42px;
}

.contact__container__box__list {
  display: flex;
  padding-top: 10px;
}

.contact__container-element-list-item {
  color: var(--color-grey);
  font-size: 16px;
  padding: 0 35px 15px 35px;
}

.contact__container-element-list-item:nth-child(1) {
  background: url(../assets/mail-icon.svg) no-repeat;
}

.contact__container-element-list-item:nth-child(2) {
  background: url(../assets/phone-icon.svg) no-repeat;
}

.contact__container {
  display: flex;
  justify-content: center;
}

.contact__container__box-left {
  width: 50%;
}

.contact__container__box-right {
  width: 50%;
  position: relative;
  display: flex;
}

.form-input {
  border: none;
  border-bottom: 1px solid var(--color-grey);
  color: var(--color-primary);
  text-transform: uppercase;
  font-size: 11px;
  width: 440px;
  height: 50px;
}

.checkbox {
  display: flex;
}

.checkbox-text {
  width: 408px;
  background-color: yellow;
}

.button-contact {
  width: 150px;
  height: 50px;
  background-color: var(--color-primary);
  border: none;
  border-radius: 23px;
  font-size: 13px;
  color: var(--color-white);
  position: absolute;
  bottom: 0;
}

footer {
  width: auto;
  height: 89px;
  background: #F7F7F7 0% 0% no-repeat padding-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 45px;
}

.company-logo {
  text-decoration: none;
  font: 30px Raleway;
  font-weight: 300;
  color: #08A6E4;
}